import {
    Container,
    Stack,
    Flex,
    Heading,
    Text,
    Image,
  } from '@chakra-ui/react';
  import image from '../Components/Images/ram_surat.png'
  import { Type } from '../Components/Type';
  export default function Home() {
    return (
      <Container maxW={'7xl'}>
        <Stack
          align={'center'}
          spacing={{ base: 8, md: 10 }}
          py={{ base: 20, md: 28 }}
          direction={{ base: 'column', md: 'row' }}>
          <Stack flex={1} spacing={{ base: 5, md: 10 }}>
            <Heading
              lineHeight={2}
              fontWeight={600}
              fontSize={{ base: '3xl', sm: '4xl', lg: '6xl' }}>
              <Text
                as={'span'}
                overflow={'hidden'}
                position={'relative'}
                fontWeight={'bold'}
                mb={8}
                _after={{
                  content: "''",
                  width: 'full',
                  height: '30%',
                  position: 'absolute',
                  bottom: 1,
                  left: 0,
                  bg: 'blue.400',
                  zIndex: -1,
                }}>
                Hi, I am Ram Surat
              </Text>
              <br />
              <Text as={'span'} color={'teal.600'} fontSize={{sm:'25px',lg:'35px'}}>
              <Type/>
              </Text>
            </Heading>
            <Text color={'gray.550'} fontSize={'23px'}>
            A Full Stack Developer who loves solving problems and building projects. I am equipped with a variety of technologies and tools to help me build the best possible product.
            </Text>
            <Stack
              spacing={{ base: 4, sm: 6 }}
              direction={{ base: 'column', sm: 'row' }}>
            </Stack>
          </Stack>
          <Flex
            flex={1}
            justify={'center'}
            align={'center'}
            position={'relative'}
            w={'full'}>
            <Image
              transform="scale(1.0)"
              transition="0.3s ease-in-out"
              // objectFit="contain"
                alt={'Hero Image'}
                fit={'cover'}
                align={'center'}
                w={{base:'50%',sm:'50%',md:'50%',lg:'50%'}}
                h={{base:'200px',sm:'300px',md:'280px',lg:'350px'}}
                border={'2px solid gray'}
                borderRadius={'10%'}
                src={image}
                _hover={{
                    transform: 'scale(1.05)',
                  }}
              />
          </Flex>
        </Stack>
      </Container>
    );
  }
  