import {
  Box,
  Flex,
  HStack,
  IconButton,
  Button,
  useDisclosure,
  useColorModeValue,
  Stack,
  useColorMode,
} from "@chakra-ui/react";
import resume from "../Components/Ram_Surat_Resume.pdf";
import { HamburgerIcon, CloseIcon, MoonIcon, SunIcon } from "@chakra-ui/icons";
import { NavLink } from "react-router-dom";
import { SlDocs } from "react-icons/sl";
import "./Navbar.css";
const Links = [
  { path: "/", title: "Home" },
  { path: "/about_me", title: "About Me" },
  { path: "/skills", title: "Skills" },
  { path: "/project", title: "Project" },
  { path: "/contact", title: "Contact" },
];

export default function Navbar() {
  const { colorMode, toggleColorMode } = useColorMode();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const onButtonClick = () => {
    window.open(
      "https://drive.google.com/file/d/1v3p4pp2foRWi8MILyLki7UxmaAHwXL9C/view?usp=sharing"
    );
  };

  return (
    <div id="nav-menu">
      <Box bg={useColorModeValue("teal", "#1a202c")} px={4}>
        <Flex h={16} alignItems={"center"} justifyContent={"space-between"}>
          <IconButton
            size={"md"}
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            aria-label={"Open Menu"}
            display={{ md: "none" }}
            onClick={isOpen ? onClose : onOpen}
          />
          <HStack
            spacing={8}
            alignItems={"center"}
            fontSize={{ base: "lg", md: "lg", lg: "2xl" }}
            fontWeight={"bold"}
          >
            <Box>RAM_SURAT</Box>
            <HStack
              as={"nav"}
              spacing={10}
              display={{ base: "none", md: "flex" }}
            >
              {Links.map((link) => (
                <NavLink
                  style={({ isActive }) => {
                    return isActive ? { color: "skyblue" } : null;
                  }}
                  key={link.path}
                  to={link.path}
                >
                  <Button fontSize={'18px'} fontWeight={'bold'} >{link.title}</Button>
                </NavLink>
              ))}
            </HStack>
          </HStack>
          <Flex alignItems={"center"} columnGap={5}>
            <Button
              onClick={toggleColorMode}
              style={{ border: "1px solid gray", borderRadius: "10px" }}
            >
              {colorMode === "light" ? <MoonIcon /> : <SunIcon />}
            </Button>
            <Button
              backgroundColor="teal"
              _hover={{ bg: "#a891b7", color: "black" }}
              color="white"
              variant={"solid"}
              colorScheme={"teal"}
              mr={4}
              leftIcon={<SlDocs />}
              onClick={onButtonClick}
              size={["sm", "md"]}
              download="Ram_Surat_Resume"
              id="resumeBtn"
            >
              <a
                href={resume}
                target="_blank"
                rel="noreferrer"
                download="Ram_Surat_Resume"
              >
                RESUME
              </a>
            </Button>
          </Flex>
        </Flex>

        {isOpen ? (
          <Box pb={4} display={{ md: "none" }}>
            <Stack as={"nav"} spacing={4}>
              {Links.map((link) => (
                <NavLink key={link.path} to={link.path}>
                  {link.title}
                </NavLink>
              ))}
            </Stack>
          </Box>
        ) : null}
      </Box>
    </div>
  );
}
